import { getOrderHistoryList, getStoreConfig } from '@/account/api';
import { OrderDetails } from '@/account/data/models';
import { UseOrdersListProps } from '@/account/types';
import { useEffect, useState } from 'preact/hooks';

export const useOrdersList = ({
  ordersInMinifiedView,
  minifiedView,
  pageSize,
  selectedDate,
  selectedPage,
  handleSetFirstOrderDate,
}: UseOrdersListProps) => {
  const [orderHistoryListItems, setOrderHistoryListItems] = useState<
    OrderDetails[]
  >([]);
  const [pageInfo, setPageInfo] = useState<{
    totalPages: number;
    currentPage: number;
    pageSize: number;
  }>({ totalPages: 1, currentPage: 1, pageSize: 1 });
  const [loading, setLoading] = useState(false);
  const [placeholderImage, setPlaceholderImage] = useState('');

  useEffect(() => {
    getStoreConfig().then((response) => {
      setPlaceholderImage(response.baseMediaUrl);
    });
  }, []);

  useEffect(() => {
    setLoading(true);

    getOrderHistoryList(
      minifiedView ? ordersInMinifiedView : pageSize,
      selectedDate,
      selectedPage
    )
      .then((response) => {
        if (!response || !response.items) return;

        setPageInfo(response.pageInfo);
        setOrderHistoryListItems(response.items);
        handleSetFirstOrderDate?.(response.dateOfFirstOrder);
      })
      .finally(() => {
        setLoading(false);
      });
  }, [
    handleSetFirstOrderDate,
    minifiedView,
    ordersInMinifiedView,
    pageSize,
    selectedDate,
    selectedPage,
  ]);

  return {
    loading,
    orderHistoryListItems,
    pageInfo,
    placeholderImage,
  };
};
